.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.hero {
  position: relative;
  zoom: 1;
  width: 100%;
  min-height: 60vh;
  background-color: #2e3034;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
}
.hero:before,
.hero:after {
  content: "";
  display: table;
}
.hero:after {
  clear: both;
}
.hero__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100%;
  width: 100%;
}
.hero__wrap > * {
  flex: 1 1;
}
@media screen and (max-width: 575px) {
  .hero__wrap > * {
    flex: 1 1 100%;
  }
  .hero__wrap > *.media {
    order: 2;
  }
  .hero__wrap > *.content {
    order: 1;
  }
}
.hero__wrap .media {
  padding: 1em;
  position: relative;
  text-align: center;
}
.hero__wrap .media img {
  max-width: 600px;
  height: auto;
  margin: auto;
}
@media screen and (max-width: 991px) {
  .hero__wrap .media img {
    width: auto;
    max-height: 300px;
  }
}
@media screen and (max-width: 575px) {
  .hero__wrap .media img {
    width: auto;
    max-height: 280px;
  }
}
.hero__wrap .content {
  padding: 1em;
}
.hero__wrap .content .ct {
  display: block;
}
.hero__wrap .content .p-tl {
  margin-top: 0;
  color: #ffffff;
  font-size: 4.25em;
  margin-bottom: 0.5em;
}
.hero__wrap .content p {
  font-size: 1.7em;
  margin-top: 0;
  color: #ffffff;
  margin-bottom: 2em;
}
.hero__wrap .content .btn {
  cursor: pointer;
  color: #ffffff;
  background-color: #f8192c;
  border-color: #f8192c;
  box-shadow: 0 0 0 transparent;
  border: 1px solid #f8192c;
  padding: 1em 2em;
  font-size: 1.275em;
  line-height: 0.85em;
  border-radius: 4em;
}
.hero__wrap .content .btn:focus,
.hero__wrap .content .btn.focus {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #8d0410;
}
.hero__wrap .content .btn:hover {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.hero__wrap .content .btn:active,
.hero__wrap .content .btn.active,
.open > .dropdown-toggle.hero__wrap .content .btn {
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.hero__wrap .content .btn:active:hover,
.hero__wrap .content .btn.active:hover,
.open > .dropdown-toggle.hero__wrap .content .btn:hover,
.hero__wrap .content .btn:active:focus,
.hero__wrap .content .btn.active:focus,
.open > .dropdown-toggle.hero__wrap .content .btn:focus,
.hero__wrap .content .btn:active.focus,
.hero__wrap .content .btn.active.focus,
.open > .dropdown-toggle.hero__wrap .content .btn.focus {
  color: #ffffff;
  background-color: #b50614;
  border-color: #8d0410;
  outline: none;
}
.hero__wrap .content .btn:active,
.hero__wrap .content .btn.active,
.open > .dropdown-toggle.hero__wrap .content .btn {
  background-image: none;
}
.hero__wrap .content .btn.disabled:hover,
.hero__wrap .content .btn[disabled]:hover,
fieldset[disabled] .hero__wrap .content .btn:hover,
.hero__wrap .content .btn.disabled:focus,
.hero__wrap .content .btn[disabled]:focus,
fieldset[disabled] .hero__wrap .content .btn:focus,
.hero__wrap .content .btn.disabled.focus,
.hero__wrap .content .btn[disabled].focus,
fieldset[disabled] .hero__wrap .content .btn.focus {
  background-color: #f8192c;
  border-color: #f8192c;
  outline: none;
}
.hero__wrap .content .btn .badge {
  color: #f8192c;
  background-color: #ffffff;
}
@media screen and (max-width: 575px) {
  .hero__wrap .content {
    text-align: center;
    padding-top: 3em;
  }
  .hero__wrap .content p {
    margin-bottom: 1em;
  }
  .hero__wrap .content .p-tl {
    font-size: 3.4em;
    margin-bottom: 0;
  }
}
