.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.new-stf-sec {
  position: relative;
  width: 100%;
  background-size: cover;
  background-position: center right;
  background-repeat: no-repeat;
  zoom: 1;
  background-color: #222327;
  display: flex;
  align-items: center;
  justify-content: center ;
}
.new-stf-sec:before,
.new-stf-sec:after {
  content: "";
  display: table;
}
.new-stf-sec:after {
  clear: both;
}
.new-stf-sec .body {
  padding: 8em 1em;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  width: 100%;
}
.new-stf-sec .body .title {
  zoom: 1;
  display: block;
  font-size: 3.4em;
  margin: 0 0 0.5em;
}
.new-stf-sec .body .title:before,
.new-stf-sec .body .title:after {
  content: "";
  display: table;
}
.new-stf-sec .body .title:after {
  clear: both;
}
.new-stf-sec .body p {
  font-size: 1.7em;
}
.new-stf-sec .body .btn {
  cursor: pointer;
  color: #ffffff;
  background-color: #f8192c;
  border-color: #f8192c;
  box-shadow: 0 0 0 transparent;
  border: 1px solid #f8192c;
  padding: 1em 2em;
  font-size: 1.275em;
  line-height: 0.85em;
  border-radius: 4em;
}
.new-stf-sec .body .btn:focus,
.new-stf-sec .body .btn.focus {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #8d0410;
}
.new-stf-sec .body .btn:hover {
  outline: none;
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.new-stf-sec .body .btn:active,
.new-stf-sec .body .btn.active,
.open > .dropdown-toggle.new-stf-sec .body .btn {
  color: #ffffff;
  background-color: #d70718;
  border-color: #ce0617;
}
.new-stf-sec .body .btn:active:hover,
.new-stf-sec .body .btn.active:hover,
.open > .dropdown-toggle.new-stf-sec .body .btn:hover,
.new-stf-sec .body .btn:active:focus,
.new-stf-sec .body .btn.active:focus,
.open > .dropdown-toggle.new-stf-sec .body .btn:focus,
.new-stf-sec .body .btn:active.focus,
.new-stf-sec .body .btn.active.focus,
.open > .dropdown-toggle.new-stf-sec .body .btn.focus {
  color: #ffffff;
  background-color: #b50614;
  border-color: #8d0410;
  outline: none;
}
.new-stf-sec .body .btn:active,
.new-stf-sec .body .btn.active,
.open > .dropdown-toggle.new-stf-sec .body .btn {
  background-image: none;
}
.new-stf-sec .body .btn.disabled:hover,
.new-stf-sec .body .btn[disabled]:hover,
fieldset[disabled] .new-stf-sec .body .btn:hover,
.new-stf-sec .body .btn.disabled:focus,
.new-stf-sec .body .btn[disabled]:focus,
fieldset[disabled] .new-stf-sec .body .btn:focus,
.new-stf-sec .body .btn.disabled.focus,
.new-stf-sec .body .btn[disabled].focus,
fieldset[disabled] .new-stf-sec .body .btn.focus {
  background-color: #f8192c;
  border-color: #f8192c;
  outline: none;
}
.new-stf-sec .body .btn .badge {
  color: #f8192c;
  background-color: #ffffff;
}
